import React, { useEffect, useRef, useMemo } from "react";
import ReactQuill, { Quill } from "react-quill";
import ImageResize from "quill-image-resize-module-react";
import "react-quill/dist/quill.snow.css";
import styled from "styled-components";
import palette from "../../lib/styles/palette";
import Responsive from "../common/Responsive";
import axios from "axios";
import imageCompression from "browser-image-compression";

Quill.register("modules/imageResize", ImageResize);

const EditorBlock = styled(Responsive)`
  padding-top: 5rem;
`;

const TitleInput = styled.input`
  font-size: 2rem;
  outline: none;
  padding-bottom: 0.5rem;
  width: 100%;
  border: none;
  border-bottom: 1px solid ${palette.gray[4]};
  margin-bottom: 2rem;
`;

const QuillWrapper = styled.div`
  .ql-container {
    border-radius: 0px 0px 10px 10px;
  }

  .ql-editor {
    padding: 0px;
    min-height: 320px;
    font-size: 1.125rem;
    line-height: 1.5;
  }

  .ql-editor.ql-blank:before {
    left: 0px;
  }
`;

//   input  .
//     ,  input  .
//  input   = change 

const EditorContent = ({ placeholder, body, onChangeField }) => {
  const quillElement = useRef(null);
  const quillInstance = useRef();

  const toolbarOptions = [
    ["link", "image", "video"],
    [{ header: [1, 2, 3, false] }],
    ["bold", "italic", "underline", "strike"],
    ["blockquote"],
    [{ list: "ordered" }, { list: "bullet" }],
    [{ color: [] }, { background: [] }],
    [{ align: [] }],
  ];

  //   ,   text editor    
  const formats = [
    "header",
    "font",
    "size",
    "bold",
    "italic",
    "underline",
    "strike",
    "align",
    "blockquote",
    "list",
    "bullet",
    "indent",
    "background",
    "color",
    "link",
    "image",
    "video",
    "width",
  ];

  const imageHandler = () => {
    const input = document.createElement("input");
    input.setAttribute("type", "file");
    input.setAttribute("accept", "image/*");
    input.click();
    input.addEventListener("change", async () => {
      if (input.files) {
        let file = input.files[0];
        const options = {
          maxSizeMB: 2,
          maxWidthOrHeight: 500,
        };
        try {
          const compressedFile = await imageCompression(
            file,
            options
          );
          file = compressedFile;

          // resize  url  fileUrl 
          const promise =
            imageCompression.getDataUrlFromFile(compressedFile);
          promise.then((result) => {
            file = result;
          });
        } catch (error) {
          console.log(error);
        }

        const formData = new FormData();
        formData.append("image", file);
        const editor = quillInstance.current;
        const range = editor.getSelection(true);
        try {
          const result = await axios.post(
            "http://localhost:4000/api/upload",
            formData
          );
          const IMG_URL = result.data.url;
          editor.root.innerHTML += `<img src=${IMG_URL}>`;
        } catch (error) {
          editor.deleteText(range.index, 1);
          return;
        }
      }
    });
  };

  // useMemo      modules   =>      
  const modules = useMemo(() => {
    return {
      toolbar: {
        container: toolbarOptions,
        handlers: {
          image: imageHandler,
        },
      },
      clipboard: {
        // toggle to add extra line breaks when pasting HTML:
        matchVisual: false,
      },
      imageResize: {
        parchment: Quill.import("parchment"),
        modules: ["Resize", "DisplaySize"],
      },
    };
  }, []);

  useEffect(() => {
    quillInstance.current = new Quill(quillElement.current, {
      theme: "snow",
      modules,
      placeholder,
      formats,
    });

    const quill = quillInstance.current;
    quill.on("text-change", (delta, oldDelta, source) => {
      if (source === "user") {
        onChangeField({
          key: "body",
          value: quill.root.innerHTML,
        });
      }
    });
    /* eslint-disable-next-line */
  }, [onChangeField]);

  //    
  useEffect(() => {
    quillInstance.current.root.innerHTML = body;
    /* eslint-disable-next-line */
  }, []);

  return <div ref={quillElement}></div>;
};

const CapacityContainer = styled.div`
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
`;
const Capacity = styled.input`
  margin-left: 10px;
  width: 100px;
`;

const Editor = ({ title, body, onChangeField, capacity }) => {
  const onChangeTitle = (e) => {
    const {
      target: { value },
    } = e;
    onChangeField({ key: "title", value });
  };

  const onChangeCapacity = (e) => {
    const {
      target: { value },
    } = e;
    onChangeField({ key: "capacity", value });
  };

  return (
    <EditorBlock>
      <TitleInput
        placeholder=" "
        onChange={onChangeTitle}
        value={title}
      />

      <QuillWrapper>
        <EditorContent
          placeholder="  "
          onChangeField={onChangeField}
          body={body}
        />
      </QuillWrapper>
      <CapacityContainer>
        <span>  </span>
        <Capacity
          type="number"
          min="2"
          max="20"
          onChange={onChangeCapacity}
          value={capacity}
        />
      </CapacityContainer>
    </EditorBlock>
  );
};

export default Editor;
